import SearchBar from "@Client/components/search-bar";
import { DarkMode } from "@Client/components/utils/dark-mode";
import { FC, useState } from "react";


interface UserPageProps {

}

// 用户页面
export const UserPage: FC<UserPageProps> = () => {

  const [darkMode, setDarkMode] = useState(DarkMode.isDark() ? 1 : 0);

  const onTab = (key: number | string) => {
    if(darkMode === key) {
      return;
    }
    setDarkMode(key as number);
    DarkMode.onSwitch(darkMode === 0)
  }

  return (
    <div className='flex flex-col items-center justify-center space-y-4 bg-leaf-50 dark:bg-leaf-50-dark'>
      {/* 模式选择 */}
      <div className='tab-bar flex-none text-sm border-b'>
        <div className='btn flex-col flex-grow' aria-selected={darkMode===0} onClick={() => onTab(0)}>亮色模式</div>
        <div className='btn flex-col flex-grow' aria-selected={darkMode===1} onClick={() => onTab(1)}>深色模式</div>
      </div>

      <div className='flex items-start space-x-2 text-sm'>
        <button className='btn-solid'>你好</button>
        <button className='btn'>你好</button>
        <button className='btn border'>你好</button>
        <button className='btn border' aria-disabled>禁用</button>
      </div>

      <input placeholder='请输入书名或作者名' className='search-input'/>

      <SearchBar placeholder='请输入书名或作者名' className='flex-grow' clearable/>

      <select className='w-32 bg-red-400'>
        <option value ="volvo">Volvo</option>
        <option value ="saab">Saab</option>
        <option value="opel">Opel</option>
        <option value="audi">Audi</option>
      </select>

    </div>
  );
}

export default UserPage;
